import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { KitchenComponent } from './kitchen.component';
import { Auth3Guard } from '../../shared/guard';
const routes: Routes = [
    {
        path: '', component: KitchenComponent,
    
    children: [
        { path: '', redirectTo: 'kitchenDashboard' },
    { path: 'kitchenDashboard', loadChildren: './kitchen-dashboard/kitchen-dashboard.module#KitchenDashboardModule',canActivate: [Auth3Guard] },
    { path: 'todayMenu' , loadChildren: './today-menu/today-menu.module#TodayMenuModule',canActivate: [Auth3Guard]},
    { path: 'stock' , loadChildren: './kitchen-balance-stock/kitchen-balance-stock.module#KitchenBalanceStockModule',canActivate: [Auth3Guard]}
    ]}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class KitchenRoutingModule {
}
